import React from 'react';

import Modal from 'react-modal';
import { IMAGES } from '../utils/images';

import './gallery.css';

const customStyles = {
  content: {
    top: '50%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    marginRight: '-50%',
    transform: 'translate(-50%, -50%)',
  },
};

class ImageGallery extends React.PureComponent {

  state = { isOpen: false, selectedImage : '' };

  render() {

    const {isOpen, selectedImage} = this.state;
    
    return (
      <>
      <div className="img002Wrapper">
      {
        IMAGES.map((img, index) => {
          const onImageClick = () => this.handleShowDialog(img.src);
          return (
            this.getImage(img.src, onImageClick)
          )
        })
      }
    </div>
    {isOpen && (
      this.getModal(selectedImage)
    )}
    </>
    )
  }

  getImage = (src, onImageClick) => {
    return (
          <img
            className="img002Image"
            src={src}
            key={src}
            onClick={onImageClick}
            alt="no"
          />
    )
  }

  getModal = (image) => {

    const closeModal = () => this.handleShowDialog('');
    return (
      <Modal
      isOpen={true}
      onRequestClose={closeModal}
      style={customStyles}
      contentLabel="Example Modal"
    >
      {this.getImage(image)}
    </Modal>
    )
  }

  handleShowDialog = (selected) => {
    this.setState({ isOpen: !this.state.isOpen, selectedImage: selected });
  };
}

export default ImageGallery;
