
import React from 'react';

import ReactAudioPlayer from 'react-audio-player';

import Song from './song.mp3'

import './music.css';

class MusicHeader extends React.PureComponent {

  render() {
    return (
      <div className="musicWrapper">
        <ReactAudioPlayer
          src={Song}
          autoPlay
          controls
          loop
          // style={{width: '80%'}}
          />
      </div>
    )
  }
}

export default MusicHeader;
